@import url(https://cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.4.1/semantic.min.css);
.App {
  font-family: Roboto, Arial, Helvetica, Helvetica Neue, sans-serif;
}

.pull-right {
  float: right;
}

.ui.button {
  padding: 0.78571429em 1rem;
  font-weight: 400;
}

.ui.table thead th {
  color: #7f8693;
  cursor: auto;
  font-style: none;
  font-weight: 400;
  padding: 0.92857143em 0.78571429em;
  text-align: inherit;
  text-transform: uppercase;
  vertical-align: inherit;
  font-size: 0.86rem;
  letter-spacing: 1px;
}

